<template>
  <div id="app">
    <mainMenu> </mainMenu>
    <homePage data-aos="fade-up" ></homePage>
    <projectsPage data-aos="fade-up" ></projectsPage>
    <skillsPage data-aos="fade-up" ></skillsPage>
  </div>
</template>

<script>
import mainMenu from '@/components/mainMenu'
import projectsPage from '@/components/projectsPage'
import skillsPage from '@/components/skillsPage'
import homePage from '@/components/homePage'

export default {
  name: 'App',
  components: {
    mainMenu, projectsPage, skillsPage, homePage
  }
}
</script>

<style>
body {
  background: rgb(0, 33, 130);
  background: linear-gradient(45deg, rgba(0, 33, 130, 1) 0%, rgba(17, 45, 200, 1) 49%, rgba(59, 188, 255, 1) 100%);
}
#app {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 15px;
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
  overflow: auto;
  height: auto;
  flex-grow: 0;
}
</style>



