<template>
<div id="projectsPage">
    <section>
      <h1>{{ msg }}</h1>
    </section>
</div>
</template>

<script>
export default {
  name: 'projectsPage',
  data () {
    return {
      msg: 'These are my projects.'
    }
  },
  methods: {
    test () {
      return 1 + 2
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#projectsPage {
  display: flex;
  justify-content: center;
  align-content: center;
  height: 100vh;
  color: #ffffff;
  margin: 10px 0px;
}
section {
  margin: auto;
}
h1::after  {
  content: " 🐳";
}
</style>



