<template>
<div id="homePage">
  <section>
    <h1>{{ msg + ", made by me." }}</h1>
    <h2> {{ vueMsg }} </h2>
  </section>
</div>
</template>

<script>
export default {
  name: 'homePage',
  data () {
    return {
      msg: 'The web of today',
      vueMsg: 'powered by vue'
    }
  },
  methods: {
    test () {
      return 1 + 2
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#homePage {
  display: flex;
  justify-content: center;
  align-content: center;
  height: 100vh;
  color: #ffffff;
  margin: 10px 0px;
}
h1 {
  font-weight: bold;
}
h1::after  {
  content: " 🤟";
}
section {
  margin: auto;
}
h2 {
  font-size: 20px;
  float: left;
}
</style>



