<template>
  <nav>
    <button id="menuBtn" v-on:click="alert()">
      <i class="fas fa-bars"></i>
    </button>
    <ul id="desktopMenu">
      <a v-scroll-to="'#homePage'"><li>Home</li></a>
      <a v-scroll-to="'#projectsPage'"><li>Projects</li></a>
      <a v-scroll-to="'#skillsPage'"><li>Skills</li></a>
    </ul>
  </nav>
</template>

<script>
export default {
  name: 'mainMenu',
  data () {
    return {}
  },
  methods: {
    alert () {
      let menu = document.getElementById('desktopMenu')
      menu.style.display = menu.style.display === 'none' ? 'block' : 'none'
      console.log(menu.style.display)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  ul,
  li {
    list-style: none;
    text-decoration: none;
  }

  nav {
    display: flex;
    justify-content: center;
    position: fixed;
    right: 25px;
  }

  ul {
    display: flex;
  }

  ul li {
    font-weight: bold;
    color: #ffffff;
    margin: 10px;
    padding: 10px 20px;
    width: 75px;
    position: relative;
    -webkit-transition: all .2s ease;
    -o-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -ms-transition: all .2s ease;
    -kthtml-transition: all .2s ease;
    transition: all .2s ease;
    cursor: pointer;
    border-radius: 64px;
  }

  ul li:hover {
    transform: translate(0, -2px);
    -webkit-box-shadow: 0px 5px 10px 1px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 5px 10px 1px rgba(0,0,0,0.1);
    box-shadow: 0px 5px 10px 1px rgba(0,0,0,0.1);
  }

  .fa-bars {
    height: 30px;
    width: 30px;
    color: rgb(0, 33, 130);
    color: linear-gradient(45deg, rgba(0, 33, 130, 1) 0%, rgba(17, 45, 200, 1) 49%, rgba(59, 188, 255, 1) 100%);
  }

  #menuBtn {
    display: none;
    background: none !important;
    border: none !important;
    max-height: 30px;
    max-width: 30px;
  }

  @media all and ( max-width: 500px ){
    #desktopMenu {
      display: none;
    }
    #menuBtn {
      display: block;
    }
    nav {
      height: auto;
      justify-content: flex-end;
      border-bottom: 3px solid rgb(0, 33, 130);
      flex-direction: column;
    }
    nav ul li {
      width: auto;
    }
  }

</style>



